.companyContainer {
    margin-left: 0px !important;
    margin-right: 0px !important;
    padding-top: 10px;
}

.circle-style {
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    align-items: center;
}

.tableContainerStyle {
    margin: 0 auto;
}

.pageLabelContainer {
    border-bottom-style: solid;
    border-color: #e2e2e2;
    border-width: 1px;
    color: #4d4d51;
}

.tableRowStyle {
    border-collapse: collapse;
}

.table-data-image {
    border-radius: 50%;
    display: inline-block;
    whiteSpace: nowrap;
    width: 30px;
    height: 30px;
}

.table-data-span {
    border-style: solid;
    border-radius: 20px;
    border-width: 1px;
}

.searchBarStyle {
    border-color: #e2e2e2;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
}

.searchBarContainerStyle {
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 0 auto;
}